<template>
  <div class="follow">
    <!-- 交给组件跳转 -->
    <UcCell
      v-for="(item,index) of column"
      :key="item._id"
      :data="item"
      :data-name="'column'"
      :isAdd="true"
      @add-item="addItem(item)"
    />
    
  </div>
</template>

<script>
  import UcCell from '../components/uc-cell.vue'
  import {COLUMN} from '../store/types.js' 
  import {mapActions} from 'vuex'
  export default {
    name:'column',
    data(){return {
    }},
    components:{
      UcCell
    },
    mounted(){
      this.$store.dispatch(COLUMN,12)
    },
    computed:{
      column(){//计算属性依赖了公共仓库
        return this.$store.state.column;
      }
    },
    updated(){},
    methods:mapActions(['addItem'])
  }
</script>

<style scoped>
  .follow{
    margin-top: .3rem;
  }
</style>
